.track {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100%;
  height: 100px;
  width: 100%;
  position: relative;
  padding: 0px var(--padding-w);
  transition: all 1s;
  &_illuminated {
    z-index: 99 !important;
    transition: all 0.5s;
    filter: drop-shadow(0px 0px 30px #5e043d)
      drop-shadow(0px 20px 30px blue)
      drop-shadow(0px 20px 20px red)
      drop-shadow(0px 20px 10px #C14694);
  }
  &__controls-block {
    position: absolute;
    left: var(--padding-w);
    top: 25%;
    z-index: 1;
    transform: skewX(-10deg);
    color: #fff;
    font-size: 10px;
    &::first-letter {
      font-size: 15px;
    }
    padding: 3px;
    --color-1: #a000a0;
    --color-2: #007070;
    box-shadow: inset 0 0 5px #fff,
      /* inner white */ inset 20px 0 8px var(--color-1),
      /* inner left magenta short */ inset -20px 0 8px var(--color-2),
      /* inner right cyan short */ inset 20px 0 30px var(--color-1),
      /* inner left magenta broad */ inset -20px 0 30px var(--color-2),
      /* inner right cyan broad */ 0 0 5px #fff,
      /* outer white */ -10px 0 8px var(--color-1),
      /* outer left magenta */ 10px 0 8px var(--color-2); /* outer right cyan */
    @media screen and (max-width: 1200px) {
      top: 100%;
      transform: skewX(-10deg) translateY(-100%);
    }
  }
}
