.winners {
  background-size: cover;
  background-position: 50% 0%;
}

.table {
  position: fixed;
  top: 15%;
  right: 20%;
  background-color: var(--color-main-lighter-transperant, #c14694a9);
  padding: 20px 80px;
  min-width: 40vw;
  color: var(--color-text-primary, #ffffff);
  transform: skewX(-10deg);
  text-align: center;
  border: 3px solid var(--color-main, white);
  tr:nth-child(even) {
    background-color: var(--color-main-lighter, #C14694);
  }
  th {
    color: var(--color-text-primary);
    background-color: var(--color-main-lighter, #C14694);
    padding: 0px 4px;
  }
  &__car-preview-box {
    margin: 5px 10px;
    height: 15px;
    width: min-content;
    // aspect-ratio: 3.78;
    transform: scale(1);
  }
  &__can-be-sorted {
    &:hover {
      cursor: pointer;
      color: #ffecff;
      text-shadow: 0px 0px 10px #ff9aff;
      text-decoration: underline;
    }
  }
  &__sorted-elem {
    color: var(--color-text-secondary) !important;
  }
}

.table__sorted-elem.table__sorted-elem_asc::after {
  content: ('↑');
}

.table__sorted-elem.table__sorted-elem_desc::after {
  content: ('↓');
}

.pager {
  display: flex;
  gap: 5px;
  position: absolute;
  top: 0px;
  right: 0px;
  transform: translateY(-100%);
  > * {
    transform: skewX(0deg);
  }
}

.pager-span {
  display: block;
  position: absolute;
  transform: translateY(100%);
  bottom: 0px;
  right: 0px;
  padding: 5px;
  background-color: var(--color-main-lighter-transperant, #c14694a9);
  border: 3px solid var(--color-main, white);
}
.pager-btn {
  border: 3px solid var(--color-main, white);
  > span {
    position: relative;
    // bottom: 2px;
    height: calc(100% + 2px);
  }
  --bg-color: var(--color-main-lighter-transperant, #c14694a9);
  --transperant-color: var(--color-main, #e4229d);
}
