.garage {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-top: 64px;
  z-index: 0;
  height: 100vh;
  &__bg {
    width: 100%;
    height: calc(100% - 360px);
    border-bottom: 5px solid #550c3a;
    // box-shadow: 0px 30px 30px #C14694;
    z-index: 1;
  }
  &__track-container {
    height: 380px;
    max-height: 40vh;
    padding: 20px 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    z-index: 1;
    background-color: red;
    &::after {
      content: 'FINISH';
      writing-mode: vertical-rl;
      text-orientation: mixed;
      font-size: 120px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      // right: 10%;
      right: -25%;
      width: 10%;
      height: 140%;
      color:rgba(255, 147, 249, 1);
      // background-image:
        // linear-gradient(90deg, $color-gridlines 2px, transparent 0);
        // background-size: 4px 4px;
        // background-position: -1px -1px;
      transform: rotateX(50deg);
      top: -30%;
      animation: hue-rotate 2s linear infinite;
      box-shadow:
        0px 5px 15px red,
        10px 0px 15px red,
        0px -5px 15px red,
        -10px 0px 15px red;
      border: 2px solid violet;
      transition: all 0.7s;
    }
    &_finish {
      &::after {
        right: 10%;
      }
    }
  }
  &__winner-popup {
    position: absolute;
    right: 15px;
    top: -55%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    // width: 50px;
    // height: 10px;
    // background-color: #c1469499;
    color:rgba(255, 147, 249);
    animation: hue-rotate 2s linear infinite;
    box-shadow:
      0px 5px 15px red,
      10px 0px 15px red,
      0px -5px 15px red,
      -10px 0px 15px red;
    border: 2px solid violet;
    transition: all 0.7s;
    transform: skewX(-10deg);
  }
}

.garage-bg {
  background: transparent;
  position: relative;
  display: flex;
  justify-content: flex-end;
  &__video {
    position: absolute;
    top: -64px;
    object-fit: cover;
    object-position: 50% 85%;
    height: calc(100% + 64px);
    width: 100%;
    z-index: -1;
  }
  &__car-ctl-btn {
    padding: 2px;
    height: min-content;
    width: min-content;
    font-size: 12px;
  }
  &__car-ctl-block-number {
    border: var(--color-main, #5e043d);
    transform: skewX(-20deg);
    grid-row: 1 / span 2;
    color: var(--color-main-lighter, #c14694);
    font-size: 32px;
    font-weight: 700;
    align-self: center;
    width: 60px;
    text-align: center;
  }
  &__car-ctl-block {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;
    gap: 2px;
    padding: 5px;
    border: 3px solid var(--color-main-lighter, #c14694);
    transform: skewX(-10deg);
    background-color: var(--color-main-lighter-transperant-ultra, #c14694a9);
    > * {
      transform: skewX(0deg);
    }
  }
  &__cars-ctls-block {
    position: fixed;
    bottom: 400px;
    left: 10px;
    z-index: 999;
    min-height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 5px;
    // @media screen and (min-width: 2000px) {
    //   position: fixed;
    //   bottom: 2.5%;
    //   left: 0px;
    //   z-index: 999;
    // }
  }
  &__self-ctls-block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__pager {
    display: flex;
    gap: 5px;
    position: absolute;
    right: 10px;
    top: 0px;
    transform: translateY(calc(-100% - 5px));
    z-index: 99;
  }
  &__pager-span {
    outline: 2px solid var(--color-main, white);
    background-color: var(--color-main-transperant, #c14694a9);
    border: 1px solid var(--color-main, white);
    transform: skewX(-20deg);
    padding: 0px 5px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-text-primary-darken, #ffffff);
  }
  &__pager-btn {
    outline: 2px solid var(--color-main, white);
    background-color: var(--color-main-transperant, #c14694a9);
    &:disabled:hover {
      opacity: 0.3;
    }
    &:hover {
      opacity: 0.9;
    }
    // background: var(--transperant-color);
  }
}

.synthwave-background {
  $color-gridlines: #c14694;
  position: relative;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #011627;
  background-image: linear-gradient(
    180deg,
    #3f0329 3%,
    transparent 30%,
    rgba(255, 147, 249, 0.4)
  );
  perspective: 100vh;
  &:before {
    content: '';
    position: absolute;
    left: -25%;
    width: 150%;
    height: 140%;
    background-image: linear-gradient($color-gridlines 2px, transparent 0),
      linear-gradient(90deg, $color-gridlines 2px, transparent 0),
      linear-gradient($color-gridlines 1px, transparent 0),
      linear-gradient(90deg, $color-gridlines 1px, transparent 0);
    background-size: 200px 200px, 200px 200px, 40px 40px, 40px 40px;
    background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
    transition: all 1s ease;
    border-top: 2px solid $color-gridlines;
    border-bottom: 2px solid $color-gridlines;
    transform: rotateX(50deg);
    top: -30%;
  }
  &_race-mode::before {
    animation: race 20s forwards;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
  }
}

@keyframes hue-rotate {
  0% {
    filter: hue-rotate(90deg);
  }
  25% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(-90deg);
  }
  75% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(90deg);
  }
}

@keyframes race {
  from {
    background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
  }
  to {
    background-position: -80000px -2px, -80000px -2px, -80001px -1px,
      -80001px -1px;
  }
}

@keyframes move-grid {
  0% {
    transform: scale(0) rotateX(50deg);
    top: -28%;
  }
  100% {
    transform: scale(1) rotateX(50deg);
    top: -28%;
  }
}
