.root {
  display: flex;
  gap: 10px;
  width: 100%;
  & > * {
    transform: skewX(-20deg);
  }
  &:hover > *:not(*:disabled) {
    opacity: 0.9;
  }
}

.text-input {
  padding: 10px;
  opacity: 0.3;
  color: var(--color-text-primary, #ffffff);
  background-color: var(--color-main-lighter-transperant);
  @media screen and (max-width: 799px) {
    width: 100px;
  }
}

.color-input {
  opacity: 0.1;
  height: 100%;
  background-color: var(--color-main-lighter-transperant);
}
