.container {
  animation: hue-rotate 2s linear infinite;
  width: 60vw;
  height: 5vh;
}

.bar {
  --progress: 0%;
  height: 100%;
  width: var(--progress);
  background: linear-gradient(to right, red, rgb(253, 86, 253));
}

.body {
  transform: skewX(-20deg);
}

.text {
  color: #C14694;
  font-size: 20px;
  font-family: 'Audiowide';
}

@keyframes hue-rotate {
  0% {
    filter: hue-rotate(60deg);
  }
  25% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(-60deg);
  }
  75% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(60deg);
  }
}
