.popup-modal {
  position: fixed;
  z-index: 9999999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.popup-bg {
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.6);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-body {
  width: auto;
  height: auto;
  padding: 30px;
  background-color: rgba(139, 0, 139, 0.4);
  border-radius: 5px;
  position: relative;
  &-header {
    color: white;
    font-size: x-large;
    text-align: center;
  }
  &-text {
    color: white;
    font-size: large;
  }
  &__btn-close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 15px;
    height: 15px;
    filter: invert(100%);
    &:hover {
      filter: invert(100%) drop-shadow(0px 0px 10px white);
      transform: scale(1.2);
    }
  }
}
