* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

html,
body {
  padding: 0px;
  margin: 0px;
  height: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input {
  border: 0px;
  &:focus {
    outline: none;
  }
  &[type='number'] {
    -moz-appearance: textfield;
    /* Chrome, Safari, Edge, Opera */
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
}

button {
  cursor: pointer;
}

@font-face {
  font-family: "Audiowide";
  src: url("./../public/assets/fonts/Audiowide-Regular.ttf");
}

.app {
  --color-bg-primary: #6e6e6e;
  --color-bg-secondary: #cecdcd;
  --color-main: #5e043d;
  --color-main-transperant: #5e043da2;
  --color-main-lighter: #C14694;
  --color-main-lighter-transperant: #c14694a9;
  --color-main-lighter-transperant-stronger: #c14694a9;
  --color-main-lighter-transperant-ultra: #c1469488;
  --color-select: #8a0258;
  --color-text-primary: #ffffff;
  --color-text-primary-darken: #dadada;
  --color-text-secondary: #5e043d;
  --color-text-secondary-lighter: #9e0164;
  --color-main-disabled: #360223;
  --color-text-disabled: #5c5b5b;
  font-family: Audiowide;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: auto auto;
}

.main-component {
  width: 100%;
  height: 100%;
  background-color: var(--color-bg-primary, #ffffff);
}

.btn {
  box-sizing: border-box;
  color: var(--color-text-primary, #fff);
  width: 130px;
  height: 40px;
  padding: 10px 25px;
  font-family: "Audiowide";
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  transform: skewX(-20deg);
  &:disabled {
    cursor: default;
    &::before {
      display: none;
    }
    &::after {
      display: none;
    }
  }
  @media screen and (max-width: 799px) {
    width: 100px;
    height: 40px;
    padding: 2px 5px;
  }
  @media screen and (max-width: 450px) {
    width: min-content;
    height: min-content;
  }
}

/* 1 */
.btn-1 {
  --color: var(--color-main, #fff);
  border: 2px solid var(--color);
  background-color: var(--color-main-transperant, #5e043da2);
  box-shadow: 0 0 10px var(--color);
  &:disabled {
    --color: var(--color-main-disabled);
    background-color: transparent;
    border-color: var(--color-main-transperant);
    box-shadow: none;
    color: var(--color-text-secondary);
    opacity: 0.7;
    &:hover {
      color: inherit;
      text-shadow: none;
      box-shadow: none;
    }
  }
  &:hover {
    color: var(--color-text-secondary-lighter, #5e043d);
    text-shadow: 0px 0px 10px #ffffff;
    box-shadow: 0 0 20px var(--color), 0 0 20px #fff inset;
  }
}

/* 4 */
.btn-4 {
  --color: #e02929;
  color: var(--color);
  padding: 0;
  border: 1px solid var(--color);
  box-shadow: 0 0 5px var(--color), 0 0 5px inset;
  transition: opacity 0.3s;
  &:disabled {
    opacity: 0.5;
  }
  &:after {
    position: absolute;
    content: "";
    top: calc(15% - 1px);
    left: calc(5% - 1px);
    width: 90%;
    height: 70%;
    border: 1px solid var(--color);
    box-shadow: 0 0 5px var(--color) inset;
    opacity: 0;
    transition: all 0.3s ease;
  }
  &:hover:after {
    opacity: 1;
  }
}

/* 6 */
.btn-6 {
  --bg-color: var(--color-main, #5e043d);
  --transperant-color: var(--color-main-lighter-transperant, #c14694a9);
  background: var(--bg-color);
  line-height: 40px;
  padding: 0;
  border: none;
  box-shadow: 0 0 20px var(--bg-color);
  &:disabled {
    background: var(--transperant-color);
    opacity: 0.3;
    &:hover::before,
    &:hover::after {
      height: 0%;
    }
    span {
      &:hover::before,
      &:hover::after {
        height: 0%;
        width: 0%;
        box-shadow: none;
      }
    }
  }
  &:hover {
    background: var(--transperant-color);
    &:active {
      background: var(--bg-color);
    }
    &:before {
      transition: all 0.8s ease-in-out;
      height: 100%;
    }
    &:after {
      transition: all 0.8s ease-in-out;
      height: 100%;
    }
  }
  &:after {
    left: 0;
    bottom: 0;
    transition: all 0.8s ease-in-out;
  }
  &:before {
    right: 0;
    top: 0;
    transition: all 0.8s ease-in-out;
  }
  &::before,
  &::after {
    position: absolute;
    content: "";
    height: 0%;
    width: 3px;
    background: var(--bg-color);
    box-shadow: 0 0 20px var(--bg-color);
  }
  span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    &:after {
      right: 0;
      bottom: 0;
      width: 0%;
      height: 2px;
      transition: all 0.8s ease-in-out;
    }
    &:before {
      left: 0;
      top: 0;
      width: 0%;
      height: 2px;
      transition: all 0.8s ease-in-out;
    }
    &:before,
    &:after {
      position: absolute;
      content: "";
      background: var(--bg-color);
      box-shadow: 0 0 5px var(--bg-color);
    }
    &:hover {
      &:before {
        width: 100%;
      }
      &:after {
        width: 100%;
      }
    }
  }
}

/* 9 */
.btn-9 {
  --color: #fff352;
  --hover-color: #00ffea;
  color: var(--color);
  border: 1px solid var(--color);
  box-shadow: 0 0 5px var(--color), 0 0 5px var(--color) inset;
  z-index: 2;
  transition: all 0.3s ease;
  overflow: hidden;
  &:disabled {
    --color: #aca43d;
    --hover-color: var(--color);
  }
  &:after {
    position: absolute;
    content: " ";
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease;
  }
  &:hover {
    color: var(--hover-color);
    &:after {
      background: var(--color);
      -webkit-transform: scale(2) rotate(180deg);
      transform: scale(2) rotate(180deg);
      box-shadow:  4px 4px 6px 0 rgba(255,255,255, 1),
                  -4px -4px 6px 0 rgba(116, 125, 136, .2),
        inset -4px -4px 6px 0 rgba(255,255,255, 1),
        inset 4px 4px 6px 0 rgba(116, 125, 136, .3);
    }
  }
}

/* 13 */
.btn-13 {
  color: #0bf4f3;
  box-shadow: 0 0 5px #0bf4f3, 0 0 5px #0bf4f3 inset;
  border: 1px solid #0bf4f3;
  z-index: 1;
  &:disabled {
    color: #0bf4f473;
    box-shadow: 0 0 2px #0bf4f473, 0 0 2px #0bf4f473 inset;
    border: 1px solid transparent;
  }
}
.btn-13:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  box-shadow: 0 0 5px #0bf4f3, 0 0 5px #0bf4f3 inset;
  transition: all 0.3s ease;
}
.btn-13:hover:after {
  top: 0;
  height: 100%;
}
.btn-13:active {
  top: 2px;
}

/* 15 */
.btn-15 {
  --color: #ff9aff;
  --hover-color: #ffffff;
  color: var(--color);
  border: 1px solid var(--color);
  box-shadow: 0 0 5px var(--color), 0 0 5px var(--color) inset;
  z-index: 1;
  &:disabled {
    --color: #683d68;
    --hover-color: var(--color);
  }
  &:after {
    position: absolute;
    content: "";
    width: 0;
    height: 100%;
    top: 0;
    right: 0;
    z-index: -1;
    background: var(--color);
    box-shadow:
     0 0 20px  var(--color);
    transition: all 0.3s ease;
  }
  &:hover {
    color: var(--hover-color);
    &:after {
      left: 0;
      width: 100%;
    }
  }
  &:active {
    top: 2px;
  }
}

.glitched-text {
  position: relative;
  &::before, &::after {
    content: attr(data-text-content);
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    top: 0;
  }
  &::before {
    left: 3px;
    text-shadow: -2px 0 red;
    animation-name: glitch-animation-1;
    animation-duration: 1.5s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
  }
  &::after {
    left: -3px;
    text-shadow: -2px 0 blue;
    animation-name: glitch-animation-2;
    animation-duration: 1.5s;
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
  }
}

@keyframes glitch-animation-1 {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip-path: inset( percentage(random(100)/100) 0% percentage(random(100)/100) 0% );
    }
  }
}

@keyframes glitch-animation-2 {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip-path: inset( percentage(random(100)/100) 0% percentage(random(100)/100) 0% );
    }
  }
}
