.header {
  width: 100%;
  height: 64px;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  opacity: 0.5;
  position: fixed;
  right: 0px;
  z-index: 1;
  transition: all 0.5s;
  &:hover {
    // background-color: var(--color-main, rgba(139, 0, 139, 0.85));
  }
  &__logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80px;
    span {
      font-size: medium;
      height: 20px;
      width: 100%;
      text-align: center;
      text-transform: uppercase;
      &:nth-child(1) {
        color: var(--color-text-primary, white);
      }
      &:nth-child(2) {
        color: var(--color-text-secondary, rgba(139, 0, 139, 0.85));
        background-color: var(--color-bg-secondary, white);
      }
    }
  }
  &-nav {
    height: 100%;
    width: fit-content;
    &__item {
      height: 100%;
    }
    &__anchor {
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 5px;
      align-items: center;
      height: 100%;
      width: calc(100% + 20px);
      padding: 0px 10px;
      text-transform: uppercase;
      span {
        padding: 10px;
      }
      &_active {
        span {
          border-radius: 3px;
          background-color: var(--color-select, rgba(139, 0, 139, 0.55));
        }
      }
    }
    &__svg {
      height: 20px;
      width: 20px;
    }
    &__span {
      color: white;
      text-decoration: none;
    }
    &__list {
      display: flex;
      gap: 20px;
      height: 100%;
      &_disabled {
        z-index: -1;
      }
    }
  }
}
